button {
    position: relative;
    display: inline-block;
    cursor: pointer;
    outline: none;
    border: 0;
    vertical-align: middle;
    text-decoration: none;
    font-size: inherit;
    font-family: inherit;
}
button.button-run {
    font-weight: 600;
    color: #382b22;
    text-transform: uppercase;
    padding: 0.4em 0.5em;
    background: #fff0f0;
    border: 2px solid #c9d6ff;
    border-radius: 0.75em;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    transition: background 150ms cubic-bezier(0, 0, 0.58, 1),
        -webkit-transform 150ms cubic-bezier(0, 0, 0.58, 1);
    transition: transform 150ms cubic-bezier(0, 0, 0.58, 1),
        background 150ms cubic-bezier(0, 0, 0.58, 1);
    transition: transform 150ms cubic-bezier(0, 0, 0.58, 1),
        background 150ms cubic-bezier(0, 0, 0.58, 1),
        -webkit-transform 150ms cubic-bezier(0, 0, 0.58, 1);
    letter-spacing: 2px;
}
button.button-run::before {
    position: absolute;
    content: "";
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: #fe2bfe;
    border-radius: inherit;
    box-shadow: 0 0 0 1px #c9d6ff, 0 0.625em 0 0 #4376ef;
    -webkit-transform: translate3d(0, 0.75em, -1em);
            transform: translate3d(0, 0.75em, -1em);
    transition: box-shadow 150ms cubic-bezier(0, 0, 0.58, 1),
        -webkit-transform 150ms cubic-bezier(0, 0, 0.58, 1);
    transition: transform 150ms cubic-bezier(0, 0, 0.58, 1),
        box-shadow 150ms cubic-bezier(0, 0, 0.58, 1);
    transition: transform 150ms cubic-bezier(0, 0, 0.58, 1),
        box-shadow 150ms cubic-bezier(0, 0, 0.58, 1),
        -webkit-transform 150ms cubic-bezier(0, 0, 0.58, 1);
}
button.button-run:hover {
    background: #8528fc;
    -webkit-transform: translate(0, 0.25em);
            transform: translate(0, 0.25em);
}
button.button-run:hover::before {
    box-shadow: 0 0 0 1px #c9d6ff, 0 0.5em 0 0 #4376ef;
    -webkit-transform: translate3d(0, 0.5em, -1em);
            transform: translate3d(0, 0.5em, -1em);
}
button.button-run:active {
    background: #8528fc;
    -webkit-transform: translate(0em, 0.75em);
            transform: translate(0em, 0.75em);
    color: #ccc;
}
button.button-run:active::before {
    box-shadow: 0 0 0 1px #c9d6ff, 0 0 #4376ef;
    -webkit-transform: translate3d(0, 0, -1em);
            transform: translate3d(0, 0, -1em);
}
